import Vue from 'vue'
import VueRouter from 'vue-router'
import DefaultLayout from '../layout/default.vue'
import { message } from 'ant-design-vue'
const RouterView = {
  template: '<router-view></router-view>',
}

Vue.use(VueRouter)

window.chkVersion = (version) => {
  let _qypt_version = localStorage._qypt_version || window._qypt_version
  window._qypt_version = version
  if (_qypt_version !== version) {
    localStorage._qypt_version = version
    message.loading('系统更新', 1, () => {
      // 刷新页面
      location.reload(true)
    })
  }
}

const compareVersion = function () {
  if (!window._qypt_version) {
    var versionScript = document.createElement('script')
    versionScript.src = 'v.js?v=' + Date.now()
    var s = document.getElementsByTagName('script')[0]
    s.parentNode.insertBefore(versionScript, s)
  }
}

const routes = [
  {
    path: '',
    redirect: '/login',
  },
  {
    path: '/system',
    name: '系统管理',
    component: DefaultLayout,
    children: [
      {
        path: '',
        redirect: '/system/user',
      },
      {
        path: 'user',
        name: '用户管理',
        component: () => import('../views/system/user.vue'),
      },
      {
        path: 'role',
        name: '角色管理',
        component: () => import('../views/system/role.vue'),
      },
      {
        path: 'auth',
        name: '权限管理',
        component: () => import('../views/system/auth.vue'),
      },
    ],
  },
  {
    path: '/school',
    name: '学校管理',
    component: DefaultLayout,
    children: [
      {
        path: 'list',
        name: '学校列表',
        component: () => import('../views/school/list.vue'),
      },
    ],
  },
  {
    path: '/resource',
    name: '资源管理',
    component: DefaultLayout,
    children: [
      {
        path: 'category',
        name: '资源分类',
        component: () => import('../views/resource/category.vue'),
      },
      {
        path: 'list',
        name: '资源列表',
        component: () => import('../views/resource/list.vue'),
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue'),
  },
]

const router = new VueRouter({
  routes,
})

router.beforeEach((to, from, next) => {
  compareVersion()
  window.scrollTo(0, 0)
  next()
})
export default router
